@import "https://fonts.googleapis.com/css?family=Montserrat:400,700,900&display=swap";
:root {
  --gradient: linear-gradient(135deg, #72edf2 10%, #5151e5 100%);
}

* {
  box-sizing: border-box;
  line-height: 1.25em;
}

body {
  background-image: url("weather.5fdfe06a.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
}

.body-container {
  width: 100%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0;
  font-family: Montserrat, sans-serif;
  display: flex;
}

.container {
  color: #fff;
  height: 600px;
  width: 390px;
  background-color: #222831;
  border-radius: 25px;
  flex-direction: column;
  display: flex;
  box-shadow: 0 0 70px -10px rgba(0, 0, 0, .2);
}

.weather-side {
  text-align: center;
  width: 100%;
  background-color: #b3cbee;
  border-radius: 25px;
}

.date-day {
  display: block;
}

.location {
  margin-top: 10px;
  display: inline-block;
}

.weather-icon.feather {
  height: 60px;
  width: auto;
}

.weather-temp {
  margin: 0;
  font-size: 3em;
  font-weight: 700;
}

.weather-desc {
  margin: 0;
}

.info-side {
  float: left;
  height: 100%;
  padding-top: 25px;
  position: relative;
}

.today-info {
  border-radius: 10px;
  justify-content: space-between;
  margin: 0 25px;
  padding: 15px;
  display: flex;
  box-shadow: 0 0 50px -5px rgba(0, 0, 0, .25);
}

.location-button {
  width: 100%;
  box-sizing: border-box;
  background-image: var(--gradient);
  color: #fff;
  cursor: pointer;
  -o-transition: transform 200ms ease;
  border: none;
  border-radius: 25px;
  outline: none;
  padding: 10px;
  font-family: Montserrat, sans-serif;
  font-weight: 700;
  transition: transform .2s, -webkit-transform .2s;
  box-shadow: 0 0 30px -5px rgba(0, 0, 0, .25);
}

.temp-container {
  justify-content: center;
  gap: 70px;
  display: flex;
}

.form {
  margin-bottom: 50px;
}

